/* $4.2.14 - Patterns - Content - Tooltip */
.tooltip {
  display: inline-block;
  position: relative;
}

.tooltip-content { -servicestudio-min-width: 90px; }

.tooltip-wrapper {
  background-color: var(--color-neutral-9);
  border-radius: var(--border-radius-soft);
  color: #fff;
  min-width: 80px;
  padding: var(--space-s) var(--space-base);
  position: absolute;
  text-align: center;
  z-index: 50;
  -servicestudio-position: relative;
  -servicestudio-width: 150px;

  &.top{
    bottom: calc(100% + var(--space-s));
    left: 50%;
    transform: translateX(-50%);
    -servicestudio-bottom: 130%;

    &:after{
      border: 7px solid transparent;
      border-top: 7px solid var(--color-neutral-9);
      bottom: -14px;
      content: "";
      height: 0;
      left: 50%;
      position: absolute;
      transform: translateX(-50%);
      width: 0;
    }
  }

  &.bottom{
    left: 50%;
    top: calc(100% + var(--space-s));
    transform: translateX(-50%);

    &:after{
      border: 7px solid transparent;
      border-bottom: 7px solid var(--color-neutral-9);
      content: "";
      height: 0;
      left: 50%;
      position: absolute;
      top: -14px;
      transform: translateX(-50%);
      width: 0;
    }
  }

  &.bottom-left{
    right: calc(100% - var(--space-m));
    top: calc(100% + var(--space-s));

    &:after{
      border: 7px solid transparent;
      border-bottom: 7px solid var(--color-neutral-9);
      content: "";
      height: 0;
      position: absolute;
      right: 7px;
      top: -14px;
      width: 0;
    }
  }

  &.bottom-right{
    left: calc(100% - var(--space-m));
    top: calc(100% + var(--space-s));

    &:after{
      border: 7px solid transparent;
      border-bottom: 7px solid var(--color-neutral-9);
      content: "";
      height: 0;
      left: 7px;
      position: absolute;
      top: -14px;
      width: 0;
    }
  }

  &.left{
    right: calc(100% + var(--space-s));
    top: 50%;
    transform: translateY(-50%);

    &:after{
      border: 7px solid transparent;
      border-left: 7px solid var(--color-neutral-9);
      content: "";
      height: 0;
      left: 100%;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 0;
    }
  }

  &.right{
    left: calc(100% + var(--space-s));
    top: 50%;
    transform: translateY(-50%);

    &:after{
      border: 7px solid transparent;
      border-right: 7px solid var(--color-neutral-9);
      content: "";
      height: 0;
      position: absolute;
      right: 100%;
      top: 50%;
      transform: translateY(-50%);
      width: 0;
    }
  }

  &.top-left{
    bottom: calc(100% + var(--space-s));
    right: calc(100% - var(--space-m));

    &:after{
      border: 7px solid transparent;
      bottom: -14px;
      content: "";
      height: 0;
      position: absolute;
      right: 7px;
      width: 0;
      border-top: 7px solid var(--color-neutral-9);
    }
  }

  &.top-right{
    bottom: calc(100% + var(--space-s));
    left: calc(100% - var(--space-m));

    &:after{
      border: 7px solid transparent;
      border-top: 7px solid var(--color-neutral-9);
      bottom: -14px;
      content: "";
      height: 0;
      left: 7px;
      position: absolute;
      width: 0;
    }
  }
}

.tooltip-background {
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
  -servicestudio-display: none;
}